*{
    margin: 0;
    padding: 0;
    font-family: "Inter";
}


header{
    height: 70px;
    border-bottom: 1px solid #eee;
}

header .content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
}

.logo{
    color: #666666;
    font-family: "Fascinate";
}

.logo span{
    color: #0000FF;
    font-family: inherit;
}

.links ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.links li{
    margin-left: 20px;
}

main .title {
    margin-top: 10%;
}

.form{
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.input{
    width: 376px;
    font-size: 20px;
    padding: 10px;
    border: 2px solid #eee;
    border-radius: 8px;
}

input[type=text]:focus{
    outline-color: #c3bdbd78;
    outline-width: 1px;
}

.button{
    width: 150px;
    font-size: 20px;
    padding: 10px;
    border: 2px solid #eee;
    border-radius: 8px;
    background-color: #0000FF;
    color: #ffffff;
}

.list{
    border-top: 1px solid #F6F6F6;
    margin-top: 10px;
    padding-top: 5px;
}
.item{
    background-color: #eee;
    border-radius: 8px;
    margin-top: 5px;
    padding: 20px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
}

